@use "sass:map";
@use "../common/_breakpoint" as *;
@use "../common/_functions" as *;
@use "../setting" as *;


@if $flex-wrap-generate {

  $flex-wrap-values: (
    "nowrap": nowrap,
    "wrap": wrap,
    "wrap-reverse": wrap-reverse
  ) !default;

  @include generate-utility-css("flex-wrap", $flex-wrap-values, null, $flex-wrap-important) using ($value, $important_value) {
    flex-wrap: $value #{$important_value};
  }
}
