@use "sass:map";
@use "../common/_breakpoint" as *;
@use "../common/_functions" as *;
@use "../setting" as *;

@if $min-width-generate {
  @include generate-utility-css("min-w", $min-width-px, "px", $min-width-important) using ($value, $important_value) {
    min-width: $value + px #{$important_value};
  }

  @include generate-utility-css("min-w", $min-width-percent, "%", $min-width-important) using ($value, $important_value) {
    min-width: $value * 1% #{$important_value};
  }
}
